import Component from "vue-class-component";
import { AxiosResponse } from "axios";
import { absolutePath as backendAbsolutePath, post as backendPost } from "@/util/BackendHelper";

interface ISubject {
  name: string,
  tags: Array<string>,
}

interface IFilter {
  name: string,
  subjects: Array<ISubject>
}

interface ITag {
  subject: string,
  tag: string,
}

interface IDiscoverRecruition {
  id: string,
  author: {
    id: string,
    username: string,
    profileImageUrl: string,
  }
  title: string,
  employmentType: string,
  pay: string,
  due: string,
  relatedBranches: Array<string>
}

interface IDiscoverPool {
  id: string,
  name: string,
  summary: string,
  tags: Array<string>,
  memberCount: number,
  imageUrl: string,
}

interface IDiscoverUser {
  id: string,
  username: string,
  birthday: string,
  telno: string,
  location: string,
  email: string,
  summary: string,
  department: string,
  tags: Array<string>,
  militaryBranch: string,
  role: string,
  profileImageUrl: string,
  postCount: number,
  followersCount: number,
}

@Component
export default class DiscoverPage extends Vue {
  absolutePath = backendAbsolutePath;

  /* define variables */
  filters = [
    {
      name: " ",
      subjects: [
        {
          name: "",
          tags: ["", "", "", "", "",
            "", "", "", "", "",
            "", "", "", "", "",
            "", "", "", ""],
        },
        {
          name: "",
          tags: ["", "", "", "", "", "",
            "", "", "", "-", "-",
            "-", "", "", "", "",
            "", "", ""],
        },
        {
          name: " ",
          tags: ["", "   ",
            "  ", "",
            "", "",
            "", "", "", ""],
        },
      ],
    },
    {
      name: "POOL",
      subjects: [
        {
          name: " ",
          tags: ["", "", "", "", "", "",
            "", "", "", "-", "-",
            "-", "", "", "", "",
            "", "", ""],
        },
        {
          name: " ",
          tags: ["", "   ",
            "  ", "",
            "", "",
            "", "", "", ""],
        },
      ],
    },
    {
      name: " ",
      subjects: [
        {
          name: " ",
          tags: [" ", " ", " ", " ", " "],
        },
        {
          name: " ",
          tags: ["", "", "", "", "", "",
            "", "", "", "-", "-",
            "-", "", "", "", "",
            "", "", ""],
        },
        {
          name: " ",
          tags: ["", "   ",
            "  ", "",
            "", "",
            "", "", "", ""],
        },
      ],
    },
  ];

  currentFilter = this.filters[0];

  tags: ITag[] = [];

  searchOptions = {
    user: [
      {
        text: "",
        value: "",
      },
      {
        text: "",
        value: "",
      },
      {
        text: " ",
        value: " ",
      },
      {
        text: " ",
        value: " ",
      },
    ],
    pool: [],
    recruition: [
      {
        text: "",
        value: "",
      },
      {
        text: "",
        value: "",
      },
      {
        text: "",
        value: "",
      },
    ],
  }

  searchOption = this.searchOptions.user;

  userDatas: IDiscoverUser[] = [];

  poolDatas: IDiscoverPool[] = [];

  recruitionDatas: IDiscoverRecruition[] = [];

  /* define methods */
  onClick1(val: IFilter): void {
    this.currentFilter = val;
    if (val.name === " ") this.searchOption = this.searchOptions.user;
    else if (val.name === "POOL") this.searchOption = this.searchOptions.pool;
    else this.searchOption = this.searchOptions.recruition;
  }

  onClick3(val: ITag): void {
    const idx = this.tags.findIndex((el) => el.subject === val.subject && el.tag === val.tag);
    if (idx > -1) console.log(idx); /* this.tags.splice(idx, 1); */
    else this.tags.push(val);
  }

  onClick4(val: ITag): void {
    const idx = this.tags.findIndex((el) => el.subject === val.subject && el.tag === val.tag);
    this.tags.splice(idx, 1);
  }

  calculateDday(val: Date): string {
    const dday = Math.floor((val.getTime() - new Date().getTime()) / (1000 * 60 * 60 * 24)) + 1;
    if (dday < 0) return "";
    if (dday === 0) return "D - day";
    return `D - ${dday}`;
  }

  async search(): Promise<void> {
    const currentSubjects = this.currentFilter.subjects.map((_subject) => _subject.name);
    const tags = this.tags.filter((_tag) => currentSubjects.indexOf(_tag.subject) > -1);
    let target = null;
    switch (this.currentFilter.name) {
      case " ":
        target = "user";
        break;
      case "POOL":
        target = "pool";
        break;
      case " ":
        target = "recruition";
        break;
      default:
        target = null;
    }
    const post = {
      type: "discover",
      target,
      tags,
      number: 30,
    };

    const response = await backendPost("/posts/discover", post) as AxiosResponse<{data: Array<unknown>}>;

    if (response.status >= 400) {
      // ERROR HANDLING
    } else if (post.target === "user") {
      this.userDatas = response.data.data as Array<IDiscoverUser>;
    } else if (post.target === "pool") {
      this.poolDatas = response.data.data as Array<IDiscoverPool>;
    } else if (post.target === "recruition") {
      this.recruitionDatas = response.data.data as Array<IDiscoverRecruition>;
    }
  }
}
